import { Component, OnInit } from '@angular/core';
import {
	FormGroup,
	FormBuilder,
	FormControl,
	Validators
} from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { FileUploader } from 'ng2-file-upload';
import Swal from 'sweetalert2'
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';

import { AuthenticationService } from '../../../../core/services/authentication.service';
import { FileBrowserService } from '../../../../core/services/file-browser.service';

import { environment } from '../../../../../environments/environment';

@Component({
	selector: 'app-upload-files-model',
	templateUrl: './upload-files-model.component.html',
	styleUrls: ['./upload-files-model.component.scss']
})

export class UploadFilesModelComponent implements OnInit {
	private apiUrl = environment.apiUrl;
	public uploader:FileUploader;
	public onClose: Subject<boolean>;
	hasBaseDropZoneOver:boolean;
	current_folder: number;
	action: any;
	loginToken: any;
	attachmentsArr = [];

	constructor(
		public translate: TranslateService,
		public bsCreateFileModalRef: BsModalRef,
		private toastr: ToastrService,
        private http: HttpClient,
		private fbService: FileBrowserService,
		private authenticationService: AuthenticationService,
	) {
		this.loginToken = this.authenticationService.currentTokenValue;
	}

	ngOnInit() {
		this.uploader = new FileUploader({
			url: this.apiUrl + '/api/files/upload',
			authToken: this.loginToken.token_type + ' ' + this.loginToken.token,
			additionalParameter: { folder: this.current_folder, action:this.action },
			method:'post',
			removeAfterUpload: false,
			autoUpload:true,
			isHTML5:true,
		});

		this.hasBaseDropZoneOver = false;
		this.onClose = new Subject();
		this.uploader.onBeforeUploadItem = (item) => {
			item.withCredentials = false;
		}

        this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
            let obj = JSON.parse(response);

            if (obj.success) {
                this.attachmentsArr.push(obj.id);
                this.toastr.success(
                this.translate.instant('file_browser.messages.upload_file'),
                this.translate.instant('file_browser.title')
                );

                //  Nu l payslip  export + download lun
                if (this.action === 'payslip') {
                this.downloadPayslipZip(obj.id);
                }
            }
        };
	}
    downloadPayslipZip(fileId: number) {

        const headers = {
            Authorization: this.loginToken.token_type + ' ' + this.loginToken.token
        };

        this.http.get(
            this.apiUrl + `/api/salary/export-docx/${fileId}`,
            {
            headers: headers as any,
            responseType: 'blob',
            observe: 'response'
            }
        ).subscribe(res => {

            if (!res.body || res.body.size === 0) {
            console.error('ZIP response empty');
            return;
            }

            const blob = new Blob([res.body], { type: 'application/zip' });
            const url = window.URL.createObjectURL(blob);

            const a = document.createElement('a');
            a.href = url;
            a.download = 'PHIEU_LUONG.zip';
            document.body.appendChild(a);
            a.click();

            document.body.removeChild(a);
            window.URL.revokeObjectURL(url);

        }, err => {
            console.error(err);
            this.toastr.error('Khng th ti file phiu lng');
        });
        }
	public fileOverBase(e:any):void {
		this.hasBaseDropZoneOver = e;
	}

	public uploaderRemove(id){
		let index = this.attachmentsArr.indexOf(id);
		if (index > -1) {
			let dataObj = {
				id: id,
			};
			this.fbService.removeAttachments(dataObj).subscribe(data => {
				this.toastr.success(this.translate.instant('file_browser.messages.delete_file'), this.translate.instant('file_browser.title'));
			});
			this.attachmentsArr.splice(index, 1);
		}
	}

	onCancel(){
		this.onClose.next(false);
		this.bsCreateFileModalRef.hide();
	}

}
